import React from "react";
import {
  BrowserRouter as Router,
  HashRouter,
  Route,
  Redirect,
  Switch,
} from "react-router-dom";
import Home from "Routes/Home";
import TV from "Routes/TV/TVContainer";
import Search from "Routes/Search/SearchContainer";
import Detail from "Routes/Detail/DetailContainer";
import Header from "Components/Header";

export default () => (
  <HashRouter>
    <>
      <Header />
      <Switch>
        {/* SwitchÎäî Ïò§ÏßÅ ÌïòÎÇòÏùò RouteÎßå RenderÌïòÍ≤å Ìï¥Ï§å */}
        <Route path="/" exact component={Home} />
        {/* exact Î•º ÎÑ£ÏßÄÏïäÏúºÎ©¥ tvÎ°ú ÏãúÏûëÌïòÎäî ÏïÑÎ¨¥Ï£ºÏÜåÍ∞Ä Îã§ Îß§Ïπ≠Îê® */}
        <Route path="/tv" exact component={TV} />
        <Route path="/search" exact component={Search} />
        <Route path="/movie/:id" exact component={Detail} />
        <Route path="/show/:id" exact component={Detail} />
        <Redirect from="*" to="/" />
        {/* ÏùºÏπòÌïòÎäî Ï£ºÏÜåÍ∞Ä ÌïòÎÇòÎèÑ ÏóÜÏúºÎ©¥ Redirect Î∂ÄÎ∂Ñ ÏûëÎèô  */}
      </Switch>
    </>
  </HashRouter>
);
